.history {
    margin: 20px 20px 0px 20px;
}

.day  {
    font-size: 20px;
    color: white;
    margin: 0 0 0 0;
    display: inline;
    border-radius: 5px;
    padding: 5px;
}

.text {
    font-size: 22px;
    font-weight: 540;
    color: white;
    margin: 5px auto 30px 0;
    padding: 30px;
    background:rgba(0,0,0,0);
    border-style: solid;
    border-image: linear-gradient(to right, #33f0a1 0%, rgb(231, 141, 243) 100%);
    border-image-slice: 1;
}

.text:hover {
    border-image-width: 5px;
    cursor: default;
}